export default "import React from 'react';\nimport Field from 'terra-form-field';\nimport Input from 'terra-form-input';\n\nconst FileExample = () => (\n  <div>\n    <Field label=\"File\">\n      <Input\n        name=\"file input\"\n        type=\"file\"\n        ariaLabel=\"File\"\n      />\n    </Field>\n  </div>\n);\n\nexport default FileExample;\n"